<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tab = 1">保护油气层钻井液性能</div>
      <div :class="{active:tab == 2}" @click="tab = 2">油气层保护材料及相匹配的其他处理剂</div>
      <div :class="{active:tab == 3}" @click="tab = 3">油气层浸泡时间及保护措施</div>
    </div>
    <Lay v-show="tab==1" :route="route" :arr="arr1" :url="url1"/>
    <Lay v-show="tab==2" :route="route" :arr="arr2" :url="url2"/>
    <Lay v-show="tab==3" :route="route" :arr="arr3" :url="url3"/>
  </div>
</template>
<script>
  import Lay from '@/components/common/template';
  import {reservoirprotection_arr1} from '@/config/record'
  import {reservoirprotection_arr2} from '@/config/record'
  import {reservoirprotection_arr3} from '@/config/record'
  export default {
    props: ["route"],
    data() {
      return {
        url1: '/data/query/reservoirprotectmuddata',  // 保护油气层钻井液性能
        arr1: reservoirprotection_arr1,
        url2: '/data/query/reservoirprotectmeterial', // 油气层保护材料及相匹配的其他处理剂
        arr2: reservoirprotection_arr2,
        url3: '/data/query/reservoirprotectmeasure',  // 油气层浸泡时间及保护措施
        arr3: reservoirprotection_arr3,
        tab: 1
      }
    },
    components: {
      Lay
    },
    mounted: function () {
    },
    methods: {
    
    }
  }

</script>




